* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.tourist-list {
  width: 50%;
  height: 25%;
}

.card-img-top {
  width: 100%;
  height: 20vw;
  object-fit: cover;
}
.detail-image {
  max-height: 70vh;
  max-width: 70vh;
}
.badge-div {
  width: 100%;
}
.price-badge {
  padding: 0.2rem;
  height: 1rem;
}
.price-badge-2 {
  padding: 1rem;
  font-size: 1rem;
}
@media only screen and (max-width: 600px) {
  .tourist-list {
    width: 100%;
  }
  .card-img-top {
    height: 40vw;
  }
  .detail-image {
    max-height: 40vh;
    max-width: 40vh;
  }
}
